.titleGreetingContainer .mainLogo {
  overflow: hidden;
  /* Ensures the content is not revealed until the animation */
  border-right: .15em solid #FFF;
  /* The typwriter cursor */
  white-space: nowrap;
  /* Keeps the content on a single line */
  margin: 0 auto;
  /* Gives that scrolling effect as the typing happens */
  animation: typing 3.5s steps(40, end), blink-caret 0.75s step-end 5;
  animation-delay: 4s;
  margin: 0;
  opacity: 0; }

/* The typing effect */
@keyframes typing {
  from {
    width: 0;
    margin-left: 50%; }
  to {
    width: 100%;
    margin-left: 0; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }

@keyframes slideInLeft {
  from {
    opacity: 1;
    transform: translate3d(-100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInLeft {
  animation-name: slideInLeft;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInDown {
  from {
    opacity: 1;
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInDown {
  animation-name: slideInDown;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInRight {
  from {
    opacity: 1;
    transform: translate3d(100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInRight {
  animation-name: slideInRight;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInUp {
  from {
    opacity: 1;
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInUp {
  animation-name: slideInUp;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn;
  animation-fill-mode: forwards;
  opacity: 0; }

.titleGreetingContainer {
  margin: 30vh auto 0; }
  .titleGreetingContainer .mainLogo {
    color: #fff;
    font-family: "Megrim", cursive;
    font-weight: bold;
    font-size: 3rem;
    letter-spacing: 1.3rem;
    line-height: 1.3;
    margin: 2rem 0 0;
    opacity: 1;
    text-transform: uppercase; }
  .titleGreetingContainer .greetingText {
    color: rgba(255, 255, 255, 0.7);
    font-family: "PT Sans Narrow", sans-serif;
    letter-spacing: .2rem;
    font-size: 1rem;
    letter-spacing: 1rem;
    line-height: 1.5;
    margin: 0;
    text-transform: uppercase; }
  .titleGreetingContainer .codedBy {
    color: #fff;
    font-size: .7rem;
    letter-spacing: .4rem;
    margin: 2rem auto;
    text-transform: uppercase;
    width: 200px; }

@media only screen and (min-width: 700px) {
  .titleGreetingContainer {
    margin: 35vh auto 0; }
    .titleGreetingContainer .mainLogo {
      font-size: 4rem; }
    .titleGreetingContainer .greetingText {
      font-size: 1rem;
      letter-spacing: 1.3rem; }
    .titleGreetingContainer .codedBy {
      width: 100%; } }

@media only screen and (min-width: 900px) {
  .titleGreetingContainer {
    margin: 40vh auto 0; }
    .titleGreetingContainer br {
      display: none; }
    .titleGreetingContainer .mainLogo {
      line-height: 1.5;
      margin: 0; }
    .titleGreetingContainer .codedBy {
      margin: .5rem auto; } }



