section {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y; }
  section .newsContainer {
    height: 2100px;
    position: relative; }
    section .newsContainer .newsHeader {
      height: 320px; }
    section .newsContainer .titleCard {
      background-color: #20A69A;
      color: #fff;
      font-family: "PT Sans Narrow", sans-serif;
      letter-spacing: .4rem;
      padding: 1.3rem 0 .5rem 3rem;
      text-align: left;
      top: 20rem;
      max-width: 100vw; }
      section .newsContainer .titleCard h2 {
        font-weight: normal; }
    section .newsContainer .contentCard {
      background-color: #fff;
      padding: 4rem 2rem; }
    section .newsContainer .nasaAPOD {
      height: auto;
      margin-top: 2rem;
      width: 100%; }

@media only screen and (min-width: 900px) {
  section .newsContainer .newsHeader {
    height: 600px; }
  section .newsContainer .titleCard {
    padding: 1.3rem 0rem 7.5rem 3rem;
    position: absolute;
    width: 80%; }
  section .newsContainer .contentCard {
    -webkit-box-shadow: 5px 5px 12px #000;
    box-shadow: 5px 5px 12px #000;
    padding: 4rem 8rem;
    position: absolute;
    right: 0;
    top: 30rem;
    width: 60%; } }

@media only screen and (min-width: 1020px) {
  .nasaNews .newsContainer {
    max-width: 1250px;
    margin: 0 auto; }
    .nasaNews .newsContainer .titleCard {
      left: 10vw; } }



