/* The typing effect */
@keyframes typing {
  from {
    width: 0;
    margin-left: 50%; }
  to {
    width: 100%;
    margin-left: 0; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }

@keyframes slideInLeft {
  from {
    opacity: 1;
    transform: translate3d(-100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInLeft {
  animation-name: slideInLeft;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInDown {
  from {
    opacity: 1;
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInDown {
  animation-name: slideInDown;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInRight {
  from {
    opacity: 1;
    transform: translate3d(100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInRight {
  animation-name: slideInRight;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInUp {
  from {
    opacity: 1;
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInUp {
  animation-name: slideInUp;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn;
  animation-fill-mode: forwards;
  opacity: 0; }

