/* The typing effect */
@keyframes typing {
  from {
    width: 0;
    margin-left: 50%; }
  to {
    width: 100%;
    margin-left: 0; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }

@keyframes slideInLeft {
  from {
    opacity: 1;
    transform: translate3d(-100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInLeft {
  animation-name: slideInLeft;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInDown {
  from {
    opacity: 1;
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInDown {
  animation-name: slideInDown;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInRight {
  from {
    opacity: 1;
    transform: translate3d(100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInRight {
  animation-name: slideInRight;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInUp {
  from {
    opacity: 1;
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInUp {
  animation-name: slideInUp;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn;
  animation-fill-mode: forwards;
  opacity: 0; }

.mainFooter {
  background-color: #2A3140;
  color: rgba(255, 255, 255, 0.7);
  padding: 5rem 2rem 0;
  text-align: left; }
  .mainFooter header {
    position: relative; }
    .mainFooter header svg {
      fill: #20A69A;
      height: 60px;
      width: 60px; }
    .mainFooter header h2, .mainFooter header h3 {
      color: #fff; }
    .mainFooter header h2 {
      font-family: "PT Sans Narrow", sans-serif;
      letter-spacing: .5rem;
      margin-bottom: 5rem;
      text-align: center; }
    .mainFooter header h2::after {
      content: " ";
      display: block;
      position: absolute;
      height: 5px;
      background: #20A69A;
      width: 100px;
      left: 35%;
      top: calc(130% + 5%); }
    .mainFooter header h3 {
      text-transform: uppercase; }
  .mainFooter .footerItem {
    border-radius: 20px;
    font-size: 1.1rem;
    height: 280px;
    margin-bottom: 2rem;
    padding: 2rem; }
    .mainFooter .footerItem a {
      color: #20A69A; }
      .mainFooter .footerItem a svg {
        fill: #20A69A;
        height: 25px;
        margin: 0 0 -8px 0;
        -webkit-transition: margin 500ms;
        -o-transition: margin 500ms;
        transition: margin 500ms;
        width: 25px; }
    .mainFooter .footerItem a:hover svg {
      margin: 0 0 -8px 10px; }
  .mainFooter .footerItem:hover {
    background-color: #262D3B; }
  .mainFooter hr {
    background-color: rgba(255, 255, 255, 0.4);
    border: none;
    border-radius: 5px;
    height: 5px; }
  .mainFooter .footerCopyright {
    color: rgba(255, 255, 255, 0.4);
    padding: .5rem 0 4rem;
    text-align: center; }
    .mainFooter .footerCopyright a {
      color: #fff; }

@media only screen and (min-width: 560px) {
  .mainFooter {
    padding: 8rem 4rem 0; }
    .mainFooter header h2::after {
      left: 38%;
      top: 120px; }
    .mainFooter .footerItem {
      padding: 4rem; } }

@media only screen and (min-width: 625px) {
  .mainFooter header h2 {
    margin-bottom: 8rem; }
  .mainFooter header h2::after {
    left: 38%;
    top: 90px; } }

@media only screen and (min-width: 900px) {
  .mainFooter header h2::after {
    left: 45%; }
  .mainFooter .footerContentContainer .footerGridContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 0 auto;
    max-width: 1350px; } }

@media only screen and (min-width: 1200px) {
  .mainFooter .footerContentContainer .footerGridContainer {
    display: grid;
    grid-template-columns: repeat(4, 1fr); } }



