.numberNav {
  display: none;
  color: rgba(255, 255, 255, 0.7);
  font-family: "PT Sans Narrow", sans-serif;
  letter-spacing: .2rem;
  font-weight: bold;
  letter-spacing: .1rem;
  position: absolute;
  right: 1.3rem;
  z-index: 2; }
  .numberNav ul {
    list-style: none; }
    .numberNav ul li {
      margin: 1rem 0; }
    .numberNav ul a:hover, .numberNav ul a:active {
      color: #fff; }

.selected {
  color: #fff;
  font-weight: bold; }

@media only screen and (min-width: 700px) {
  .numberNav {
    display: block;
    top: 40.5vh; } }

@media only screen and (min-width: 900px) {
  .numberNav {
    display: block;
    top: 38.5vh; } }



