/* The typing effect */
@keyframes typing {
  from {
    width: 0;
    margin-left: 50%; }
  to {
    width: 100%;
    margin-left: 0; } }

/* The typewriter cursor effect */
@keyframes blink-caret {
  from, to {
    border-color: transparent; }
  50% {
    border-color: #fff; } }

@keyframes slideInLeft {
  from {
    opacity: 1;
    transform: translate3d(-100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInLeft {
  animation-name: slideInLeft;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInDown {
  from {
    opacity: 1;
    transform: translate3d(0, -100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInDown {
  animation-name: slideInDown;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInRight {
  from {
    opacity: 1;
    transform: translate3d(100%, 0, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInRight {
  animation-name: slideInRight;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes slideInUp {
  from {
    opacity: 1;
    transform: translate3d(0, 100%, 0);
    visibility: visible; }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0); } }

.slideInUp {
  animation-name: slideInUp;
  animation-fill-mode: forwards;
  opacity: 0; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

.fadeIn {
  animation-name: fadeIn;
  animation-fill-mode: forwards;
  opacity: 0; }

.mapsInformationContainer {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.5)), to(rgba(0, 0, 0, 0.5))) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  background: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)) center center repeat-y, #000 url("../../images/star-background.jpg") center center repeat-y;
  color: #fff;
  padding: 5rem 2rem 0; }
  .mapsInformationContainer .grid-container {
    padding-bottom: 5rem; }
  .mapsInformationContainer .sectionTitle, .mapsInformationContainer .mapTitle {
    font-family: "PT Sans Narrow", sans-serif;
    letter-spacing: .7rem;
    text-transform: uppercase; }
  .mapsInformationContainer .sectionTitle {
    font-size: 1.7rem;
    font-weight: normal;
    padding-bottom: 4rem; }
  .mapsInformationContainer .mapImage {
    border-radius: 50%;
    height: auto;
    width: 100%; }
  .mapsInformationContainer .mapButton {
    background-color: #20A69A;
    border-radius: 20px;
    color: #fff;
    font-family: "PT Sans Narrow", sans-serif;
    margin: 4rem auto 0;
    max-width: 450px;
    padding: .5rem;
    width: 80%;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: .3rem; }

@media only screen and (min-width: 900px) {
  .mapsInformationContainer {
    padding: 5rem 20% 0; }
    .mapsInformationContainer .grid-container {
      display: grid;
      grid-template-columns: 30% 70%;
      grid-template-rows: 350px;
      grid-column-gap: 2rem;
      max-width: 900px; }
    .mapsInformationContainer .flipped-grid-container {
      grid-template-columns: 70% 30%;
      margin-left: 8rem; }
      .mapsInformationContainer .flipped-grid-container img {
        grid-area: 1 / 2 / 1 / 3; }
      .mapsInformationContainer .flipped-grid-container div {
        grid-area: 1 / 1 / 1 / 2; }
    .mapsInformationContainer .mapInfo {
      text-align: left; }
    .mapsInformationContainer .mapButton {
      margin-left: 0; } }



