/*toggle-icon*/
.toggle-icon {
  display: block;
  position: relative;
  width: 100%;
  margin: -2.2em auto 0 0.2em;
  transition: margin 600ms;
  cursor: pointer; }

.toggle-icon .bar {
  display: block;
  width: 3.8em;
  height: 0.2em;
  margin: 0.9em;
  border-right: 2.8em solid #fff;
  border-left: 0.6em solid #d6d6d6;
  box-sizing: border-box;
  transition-property: transform, margin, border-right-color, box-shadow;
  transition-duration: 600ms; }

.toggle-icon .bar:nth-of-type(1) {
  transition: opacity 300ms; }

.toggle-icon .bar:nth-of-type(2) {
  transform: rotate(-180deg); }

/*pushed*/
.pushed .toggle-icon {
  margin: -2em 0 0 0.7em; }

.pushed .bar:nth-of-type(1) {
  opacity: 0; }

.pushed .bar:nth-of-type(2) {
  margin-left: 1.4em;
  transform: rotate(-137deg) translateY(-1.5em); }

.pushed .bar:nth-of-type(3) {
  margin-left: 0.5em;
  transform: rotate(-42deg); }

.pushed .toggle-icon:hover .bar:nth-of-type(2) {
  border-right-color: #27C9BB;
  box-shadow: 0.4em 0 0.6em 0.1em #20A69A; }

.pushed .toggle-icon:hover .bar:nth-of-type(3) {
  border-right-color: #27C9BB;
  box-shadow: 0.4em 0 0.6em 0.1em #20A69A; }

.navContainer {
  display: grid;
  grid-template-columns: repeat(2, 50px);
  grid-template-rows: 30px;
  margin: 1.5rem;
  position: absolute;
  right: 0;
  text-transform: uppercase; }
  .navContainer .navMenuIconContainer {
    font-size: .7rem;
    margin: 1rem;
    position: absolute;
    right: -90vw; }
  .navContainer .activeNavMenuIconContainer {
    margin-top: 5px;
    padding: 0;
    position: static; }
  .navContainer .mainNav {
    color: rgba(255, 255, 255, 0.7);
    font-family: "PT Sans Narrow", sans-serif;
    letter-spacing: .2rem;
    background-color: #2A3140;
    color: #fff;
    display: none;
    font-size: 1.4rem;
    height: 100vh;
    left: calc(-100vw + 100%);
    list-style: none;
    margin: 0;
    padding: 8rem 4rem 0;
    position: fixed;
    text-align: left;
    top: -1.5rem;
    width: 100vw;
    z-index: 5; }
    .navContainer .mainNav a:hover, .navContainer .mainNav a:active {
      color: #fff; }
  .navContainer .searchBarContainer {
    display: grid;
    grid-template-columns: repeat(4, 100vw/4);
    grid-template-rows: 2rem;
    left: calc(-100vw + 120%);
    margin: 0;
    position: absolute;
    width: 2rem;
    z-index: 10; }
  .navContainer .activeSearchBarContainer {
    width: 95vw; }
  .navContainer #___gcse_0 {
    position: absolute;
    top: 0; }
  .navContainer .searchIcon {
    border-radius: 20px;
    justify-self: end;
    fill: rgba(255, 255, 255, 0.6);
    grid-area: 1 / 4 / 1 / 5;
    height: 100%;
    padding: 4px;
    width: 80px; }
    .navContainer .searchIcon input {
      display: none; }
    .navContainer .searchIcon svg {
      height: 100%; }
  .navContainer .searchIcon:hover {
    fill: #fff;
    cursor: pointer; }
  .navContainer .remove {
    display: none; }
  .navContainer .reveal {
    display: block; }
  .navContainer .activeSearchIcon {
    background-color: #20A69A; }
  .navContainer .searchBar {
    border-radius: 20px;
    grid-area: 1 / 2 / 1 / 4;
    height: 0;
    margin-right: 1rem;
    overflow: hidden;
    margin-left: .5rem;
    opacity: 0;
    outline: none;
    padding: 1.1rem;
    position: absolute;
    right: -100%;
    -webkit-transition: width 1s;
    -o-transition: width 1s;
    transition: width 1s;
    width: 0;
    z-index: -2; }
  .navContainer .activeSearchBar {
    margin-right: 1rem;
    opacity: 1;
    width: 200%; }
  .navContainer .selected {
    color: #fff; }

@media only screen and (min-width: 550px) {
  .navContainer .activeSearchBar {
    margin-right: 2rem;
    width: 100%; } }

@media only screen and (min-width: 700px) {
  .navContainer .activeSearchBar {
    margin-right: 5rem;
    width: 100%; } }

@media only screen and (min-width: 900px) {
  .navContainer {
    grid-column-gap: 4rem;
    grid-template-columns: repeat(2, auto);
    grid-template-rows: 40px;
    margin: 1.5rem 2.5rem; }
    .navContainer .toggle-icon {
      display: none; }
    .navContainer .mainNav {
      background-color: transparent;
      color: rgba(255, 255, 255, 0.7);
      display: grid;
      font-size: 1.3rem;
