      grid-template-rows: 10px;
      height: 2rem;
      margin-top: .8rem;
      padding: 0;
      position: static;
      width: 110%; }
    .navContainer .searchIcon {
      grid-area: 1 / 4 / 1 / 5;
      height: 1.75rem;
      margin-top: 12px;
      width: 1.75rem; }
    .navContainer .activeSearchIcon {
      width: 4rem;
      position: absolute;
      right: -2.2rem; }
    .navContainer .searchBarContainer {
      grid-area: 1 / 2 / 1 / 3;
      margin-left: 0;
      position: static; }
    .navContainer .activeSearchBarContainer {
      margin-left: 0;
      width: 20vw; }
    .navContainer .searchBar {
      margin: 0; }
    .navContainer .activeSearchBar {
      height: 1.4rem;
      margin: 0;
      margin-top: .7rem;
      padding: 5px 10px;
      position: static;
      width: 200px; }
    .navContainer .remove {
      display: block; } }

@media only screen and (min-width: 1220px) {
  .navContainer .searchBarContainer {
    max-width: 250px; }
    .navContainer .searchBarContainer .activeSearchBar {
      position: absolute;
      right: .7rem; } }



